#header-container {
    display: flex;    
    background-color: linear-gradient(rgba(80, 61, 47, 0.3), rgba(80, 61, 47, 0.8));
    background-position: center;
    overflow: hidden;
    background-size: cover;

    color: var(--second-color);
    border-radius: 0.5rem 0.5rem 0 0;
}

#header-container.space-around {
    justify-content: space-around;
}

#branding #slogan {
    text-align: center;
}

#slogan{
    display: flex;
    flex-direction: column;
    justify-content: center;
}


#branding img {
    width: 4rem;
}

#slogan h1 {
    color: var(--second-color);
    font-weight: lighter;
}

#slogan h2 {
    font-weight:normal;
}

#branding {
    padding: 0.5rem;
    font-size: normal;
}

/* Medium devices such as tablets (768px and up) */
@media only screen and (min-width: 768px) {
    #branding img {
        width: 6rem;
    }
}

/* Large devices such as laptops (992px and up) */
@media only screen and (min-width: 992px) {
    #branding img {
        width: 8rem;
    }

    #branding {
        padding: 1rem;
        font-size: medium;
    }
    
}

/* Largest devices such as desktops (1200px and up) */
@media only screen and (min-width: 1200px) {
    #branding img {
        width: 12rem;
    }
}